import React, { Fragment } from 'react';
import PropTypes from 'prop-types';

// components
import Typography from 'components/markup/Typography';
import Icon from 'components/markup/Icon';

const renderPopupButtons = (buttons, performPopupClose) =>
	buttons.map(button =>
		(<button type="button" key={button.id} className={button.className} onClick={() => {
			performPopupClose();
		}}>{button.text}</button>));

const MessagePopup = (props) => {
	return (
		<Fragment>
			<div className="flex between-xs pad15">
				<Typography
					text={''}
					tag={'h4'}
				/>
				<button className="close" onClick={props.ifClicked}><Icon name="close" /></button>
			</div>
			<div className="popup-body text-center h100">
				{props.title && (
					<Typography
						text={props.title}
						tag={'h4'}
					/>
				)}
				{props.description && (
					<Typography
						className={'form-group'}
						text={props.description}
						tag={'p'}
					/>
				)}
				{props.buttons &&
				<div className="popup-buttons">
					{renderPopupButtons(props.buttons, props.ifClicked)}
				</div>
				}
			</div>
		</Fragment>
	);
};


MessagePopup.propTypes = {
	title: PropTypes.string,
	description: PropTypes.string,
	buttons: PropTypes.array,
	ifClicked: PropTypes.func,
};


export default MessagePopup;
