import React from 'react';
import PropTypes from 'prop-types';
import classNames from 'classnames';

const TextArea = props => {
	const containerClass = classNames('input input-fullWidth', props.className, {
		'has-error': props.errorMessage
	});
	const inputClass = classNames({
		'form-group': props.isGroup
	});

	return (
		<div className={inputClass}>
			<label htmlFor={props.id}>
				{props.label}
			</label>
			<textarea
				placeholder={props.placeholder}
				name={props.name}
				id={props.id}
				rows={props.rows}
				className={containerClass}
				maxLength={props.maxLength}
				pattern={props.pattern}
				value={props.value}
				readOnly={props.readonly}
				autoComplete={props.autocomplete}
				autoCapitalize={props.autoCapitalize}
				onChange={props.ifChanged}
				onBlur={props.onFocusOut}
				onFocus={props.ifFocussed}
				disabled={props.disabled}
			/>
			{props.children}
			{props.errorMessage && <div className={'text-right text-error'}>{props.errorMessage}</div>}
		</div>
	);
};

TextArea.propTypes = {
	id: PropTypes.string,
	rows: PropTypes.number,
	maxLength: PropTypes.number,
	className: PropTypes.string,
	name: PropTypes.string,
	isRequired: PropTypes.bool,
	label: PropTypes.string,
	children: PropTypes.any,
	pattern: PropTypes.string,
	autoCapitalize: PropTypes.string,
	value: PropTypes.string,
	placeholder: PropTypes.string,
	tooltip: PropTypes.string,
	errorMessage: PropTypes.string,
	autocomplete: PropTypes.string,
	ifFocussed: PropTypes.func,
	onFocusOut: PropTypes.func,
	ifChanged: PropTypes.func,
	ifPressed: PropTypes.func,
	ifClicked: PropTypes.func,
	isEmpty: PropTypes.bool,
	isGroup: PropTypes.bool,
	readonly: PropTypes.bool,
	hasError: PropTypes.bool,
	hasWarning: PropTypes.bool,
	disabled: PropTypes.any
};

TextArea.defaultProps = {
	className: '',
	value: '',
	placeholder: ' ',
	tooltip: '',
	label: '',
	id: '',
	icon: '',
	children: '',
	autocomplete: 'off',
	appear: true,
	hasError: false,
	endTooltip: false,
	isGroup: false,
	readonly: false,
	isEmpty: true
};

export default TextArea;
